.body {
  width: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.body-landing {
  padding: 5em 6em 3em 6em;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.body-landing-main {
  display: flex;
  align-items: flex-end;
  padding: 0 6em 8em 6em;
  width: 83vw;
  flex-wrap: wrap;
}
.body-landing-main-heading {
  font-size: 10.5em;
  font-weight: 500;
  text-align: justify;
  line-height: 1.1em;
}
.body-landing-main-heading-wrapper {
  width: 64%;
}
.body-landing-main-heading-small {
  width: 100%;
  font-size: 4.5em;
  font-weight: 100;
  text-align: justify;
  line-height: 1em;
  color: navy;
}
.body-landing-main-subheading {
  font-size: 2.3em;
  font-weight: 200;
  text-align: left;
  width: 28%;
  color: black;
  text-shadow: 1px 1px white;
}
.body-landing-main-instructions {
  align-self: flex-end;
  margin-top: 3em;
  text-align: left;
  padding-left: 0.25em;
}
.body-landing-main-instructions div {
  margin-bottom: 0.5em;
  color: white;
  text-shadow: 0 0 15px black;
  font-weight: 300;
  font-size: 1.1em;
}
.body-landing-about {
  padding: 2em 2em 2em 2em;
  display: flex;
  flex-wrap: wrap;
}
.body-landing-about-title {
  font-size: 4.5em;
  font-weight: 600;
}
.body-landing-about-one {
  font-size: 1.3em;
  font-weight: 300;
  text-align: justify;
  font-style: italic;
  align-self: flex-start;
  padding: 0.5em 0.5em 0.5em 0.5em;
  text-shadow: 0 0 10px black;
  color: white;
}
.body-landing-about-two {
  font-size: 1.3em;
  font-weight: 300;
  align-self: flex-start;
  font-style: italic;
  text-align: justify;
  padding: 0em 0.5em 0.5em 0.5em;
  text-shadow: 0 0 10px black;
  color: white;
}
.body-landing-about-three {
  align-self: center;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0 1em 0 1em;
}
.body-landing-about-three-img-wrapper {
  padding-bottom: 1em;
}
.body-landing-about-three-img-wrapper img {
  max-width: 100%;
  max-height: 100%;
  border: 10px solid whitesmoke;
  padding: 0.5em;
}
.body-landing-about-three-text {
  text-align: justify;
  font-weight: 250;
  font-size: 1.7em;
  line-height: 1.8em;
  padding: 0.5em 0 1em 1em;
  text-shadow: 0 0 5px navy;
  color: white;
}
.body-landing-about-four {
  font-size: 0.9em;
  text-align: left;
}
.body-landing-about-credits {
  font-size: 2em;
  font-weight: 600;
  text-align: right;
}
.body-steps {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.body-steps-img-wrapper {
  align-self: flex-end;
  margin-bottom: 4em;
}
.body-steps-img-wrapper img {
  max-width: 100%;
  max-height: 100%;
}
