.step {
  border-style: solid;
  border-width: 1px;
  border-color: grey;
  box-shadow: 0px 0px 1em 5px gray;
  padding: 1.75em;
  text-align: left;
  margin-bottom: 4em;
  background: rgba(240, 240, 240, 0.85);
}
.step-text-uppercase {
  text-transform: uppercase;
}
.step-text-title {
  font-size: 3.5em;
  font-weight: 700;
}
.step-text-title-roman {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto", serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}
.step-header {
  padding: 0 0.5em 0.5em 0.5em;
  display: flex;
}
.step-header-info {
  border-bottom: 1px gray solid;
  padding-right: 1em;
  display: flex;
  flex-direction: column;
}
.step-header-info-short {
  width: 42%;
}
.step-header-info-subtitle-area {
  font-size: 2.25vw;
  font-weight: 200;
  font-style: italic;
  text-align: center;
  color: navy;
}
.step-header-info-subtitle-area-wrapper {
  flex: 1 1;
  display: flex;
  align-items: center;
  padding: 0 1.25em 2em 1.25em;
}
.step-header-video-area {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
}
.step-header-video-area-wrapper {
  width: 58%;
  margin-left: 1.25em;
}
.step-header-video-area-frame {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.step-info-area {
  padding: 1em 1em 0 1em;
  display: flex;
}
